import Look from '../../components/look.vue'
export default {
	components: {
		Look
	},
	data() {
		return {
			nav: 0,
			tabList: ['工会简介', '通知公告', '工会要闻', '基层动态'],
			descList: [
				{
					title: '九江市总工会简介',
					publish: '管理员发布',
					lookNumber: '7789'
				},
				{
					title: '工会组织架构',
					publish: '管理员发布',
					lookNumber: '7789'
				}
			],
			noticeList:[
				{
					pic: '/static/imgs/index/search.png',
					title: '九江市总工会召开新任职干部集体谈话会议',
					publish: '管理员发布',
					lookNum: '7780'
				},
			],
			newsList:[
				{
					pic: '/static/imgs/index/search.png',
					title: '“夏送清凉”高温走访',
					publish: '管理员发布',
					lookNum: '7780'
				},
			],
			trendsList:[
				{
					pic: '/static/imgs/index/search.png',
					title: '市总工会组织党员干部开展无偿献血活动',
					publish: '管理员发布',
					lookNum: '7780'
				},
			]
		}
	},
	methods: {
		info(id) {
			this.$router.push({
				name: 'ArticleInfo',
				query: {
					id: id,
					title: this.tabList[0]
				}
			})
		}
	}
}



